* {
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 50px;
}

input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid{
	display: flex;
	flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.card{
  align-content: flex-start;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}